import type { Question, Difficulty, TestType, TestSettings } from '@/types';

const TEST_CONFIG: Record<TestType, Record<Difficulty, TestSettings>> = {
  'addition-subtraction': {
    easy: { numQuestions: 50, timeLimit: 300, title: 'Addition & Subtraction (Easy)', icon: 'plus-circle' },
    medium: { numQuestions: 100, timeLimit: 600, title: 'Addition & Subtraction (Medium)', icon: 'plus-circle' },
    hard: { numQuestions: 150, timeLimit: 900, title: 'Addition & Subtraction (Hard)', icon: 'plus-circle' },
  },
  multiplication: {
    easy: { numQuestions: 50, timeLimit: 300, title: 'Multiplication (Easy)', icon: 'x' },
    medium: { numQuestions: 100, timeLimit: 600, title: 'Multiplication (Medium)', icon: 'x' },
    hard: { numQuestions: 150, timeLimit: 900, title: 'Multiplication (Hard)', icon: 'x' },
  },
  division: {
    easy: { numQuestions: 50, timeLimit: 300, title: 'Division (Easy)', icon: 'divide' },
    medium: { numQuestions: 100, timeLimit: 600, title: 'Division (Medium)', icon: 'divide' },
    hard: { numQuestions: 150, timeLimit: 900, title: 'Division (Hard)', icon: 'divide' },
  },
};

export function getTestSettings(testId: TestType, difficulty: Difficulty): TestSettings | undefined {
  return TEST_CONFIG[testId]?.[difficulty];
}

function getRandomInt(min: number, max: number): number {
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

function shuffleArray<T>(array: T[]): T[] {
  for (let i = array.length - 1; i > 0; i--) {
    const j = Math.floor(Math.random() * (i + 1));
    [array[i], array[j]] = [array[j], array[i]];
  }
  return array;
}

function generateOptions(correctAnswer: number): number[] {
  const options = new Set<number>([correctAnswer]);
  const range = Math.max(10, Math.abs(Math.floor(correctAnswer * 0.2)));

  while (options.size < 4) {
    const minOption = Math.max(0, correctAnswer - range);
    const maxOption = correctAnswer + range;
    const wrongAnswer = getRandomInt(minOption, maxOption);
    
    if (wrongAnswer !== correctAnswer && wrongAnswer >= 0) {
      options.add(wrongAnswer);
    }
  }
  return shuffleArray(Array.from(options));
}

function getNumberRange(difficulty: Difficulty): [number, number] {
    switch (difficulty) {
        case 'easy': return [1, 9];
        case 'medium': return [10, 99];
        case 'hard': return [100, 9999];
        default: return [1, 9];
    }
}

export function generateTest(testId: TestType, difficulty: Difficulty): Question[] {
  const settings = getTestSettings(testId, difficulty);
  if (!settings) {
    return [];
  }

  const questions: Question[] = [];
  const [min, max] = getNumberRange(difficulty);

  for (let i = 0; i < settings.numQuestions; i++) {
    let questionText: string;
    let answer: number;

    switch (testId) {
      case 'addition-subtraction': {
        const numbers: number[] = [];
        const operations: ('+' | '-')[] = [];
        
        // Generate 4 numbers
        for (let j = 0; j < 4; j++) {
            numbers.push(getRandomInt(min, max));
        }

        // Generate 3 operations, ensuring at least one of each
        const ops = ['+', '-'];
        operations.push(ops[getRandomInt(0, 1)] as '+' | '-');
        operations.push(ops[getRandomInt(0, 1)] as '+' | '-');
        // If first two are the same, the third is different to ensure a mix
        if (operations[0] === operations[1]) {
            operations.push(operations[0] === '+' ? '-' : '+');
        } else {
            operations.push(ops[getRandomInt(0, 1)] as '+' | '-');
        }
        shuffleArray(operations);


        // Build expression and calculate answer, ensuring no negative intermediate results
        let currentResult = numbers[0];
        let expression = `${numbers[0]}`;

        for (let j = 0; j < 3; j++) {
            const nextNum = numbers[j + 1];
            const op = operations[j];

            if (op === '-' && currentResult < nextNum) {
                // To avoid negative result, we ensure the current result is larger.
                // A simple way is to swap with a previous larger number if available, or regenerate.
                // For simplicity, we'll make the first number large enough.
                const ensurePositiveMin = numbers.slice(j + 1).reduce((sum, n, idx) => operations[j+idx] === '-' ? sum + n : sum, 0);
                if (currentResult < ensurePositiveMin) {
                    numbers[0] += ensurePositiveMin;
                    // Recalculate from start
                    currentResult = numbers[0];
                    for (let k = 0; k < j; k++) {
                        if (operations[k] === '+') currentResult += numbers[k + 1];
                        else currentResult -= numbers[k + 1];
                    }
                }
            }
            
            if (op === '+') {
                currentResult += nextNum;
            } else {
                currentResult -= nextNum;
            }

            expression += ` ${op} ${nextNum}`;
        }

        // Final answer calculation
        let finalAnswer = numbers[0];
        for (let j = 0; j < 3; j++) {
            if (operations[j] === '+') {
                finalAnswer += numbers[j + 1];
            } else {
                finalAnswer -= numbers[j + 1];
            }
        }
        
        let questionTextWithCorrectedNumbers = `${numbers[0]}`;
        for(let j=0; j<3; j++) {
            questionTextWithCorrectedNumbers += ` ${operations[j]} ${numbers[j+1]}`;
        }

        questionText = questionTextWithCorrectedNumbers;
        answer = finalAnswer;
        break;
      }
      
      case 'multiplication':
        const m1_max = difficulty === 'easy' ? 9 : (difficulty === 'medium' ? 99 : 999);
        const m2_max = difficulty === 'hard' ? 99 : 9;
        const m1 = getRandomInt(min, m1_max);
        const m2 = getRandomInt(1, m2_max);
        answer = m1 * m2;
        questionText = `${m1} ร ${m2}`;
        break;

      case 'division':
        const divisor = getRandomInt(2, 9); // Divisor is always a single digit > 1
        const [answer_min, answer_max] = getNumberRange(difficulty);
        answer = getRandomInt(answer_min, answer_max);
        const dividend = divisor * answer;
        questionText = `${dividend} รท ${divisor}`;
        break;

      default:
        throw new Error('Invalid test type');
    }

    questions.push({
      text: questionText,
      options: generateOptions(answer),
      answer: answer,
    });
  }

  return questions;
}
